@media only screen
and (max-height: 420px)
and (max-width: 750px)
and (orientation: landscape) {
  main {
    max-width: 80%;
  }

  header {
    flex-direction: row;
    height: 60vh;

    .info {
      flex-direction: column;
      font-size: .7em;
    }


    > * {
      margin: 10px;
    }
  }

  figure {
    .panda-bag {
      width: 60vw;
    }

  }

  .blurb {
    padding: 0 3px;
  }

  .two-column > .main-content {
    float: none;
    width: inherit;
  }
}

@media screen
and (max-width: 415px)
and (orientation: portrait) {
  details {
    max-width: 100%;
  }
  
  aside {
    nav {
      font-size: 0.8em;

      ul > li > a.nav-link {
        padding: 8px 8px
      }
    }
  }

  main {
    min-width: 100%;
    padding: 10px;
  }

  .artifact-list {
    margin-left: 0;
  }

  .content-box {
    min-width: 100%;
    margin: 10px auto;

    .content {
      font-size: 0.8em;
    }

    .title {
      font-size: 1em;
    }

    .artifact-list {
      max-width: 100%;
      padding-left: 0px;
    }
  }

  footer {
    .email {
      font-size: 0.8em;
    }
  }

  header {
    h1 {
      font-size: 1.5em;
    }
    
    .email {
      font-size: 0.7em;
    }

    img {
      width: 30vw;
      height: auto;
    }
	}
  
  figure {
    .panda-bag {
      width: 60vw;
      height: auto;
    }
  }

  .blurb {
    min-width: 100%;
    padding: 0 0;
    font-size: 0.9em;
  }

  aside {
    width: 100%;

    nav {
      font-size: 0.7em;

      ul {
        li {
          a.nav-link {
            padding: auto 7px auto 7px;
          }
        }
      }
    }
  }

  .two-column > .main-content {
    float: none;
    width: inherit;
  }

  .post {
    article {
      width: 100%;
    }
  }

  img {
    width: 100%;
    height: auto;
  }

  .post-title {
    font-size: medium;
  }
}
