body {
  min-width: $content-width;
  margin: auto;
  color: $font-color;
  font-family: $font-style;
  -webkit-font-smoothing: antialiased;
}

h2,
h3,
h4 {
  font-weight: 600;
  color: $heading-font-color;
  font-family: $heading-font;
  margin: auto;
}

h1 {
  color: $header;
  font-family: $heading-font;
  a,
  a:visited {
    text-decoration: none;
  }
}

h3 {
  padding-top: 5px;
}

a,
a:visited {
  color: $link-color;
  text-decoration: underline;
}

a:hover {
  color: $link-hover-color;
  text-decoration: underline;
}

.blurb {
  margin-bottom: 20px;
  font-size: 1em;
  padding: 0 2em;
  max-width: 90%;
}

header {
  padding: 0px 15px;
  text-align: center;
  margin: $header-height 0 0;
  height: min(40vh, 200px);
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  font-weight: bold;

  &.pages {
    height: auto;
  }

  img {
    border-radius: 360px;
    height: min(35vh, 150px);
    max-height: 200px;
    width: auto;
  }

  h1 {
    margin-bottom: 2px;
    margin-left: 5px;
    margin-top: 0px;
  }

  .info {
    margin-left: 40px;
  }
}

main {
  padding: 0 15px;
  width: 80%;
  max-width: 80em;
  margin: 0 auto;

  &.post {
    max-width: 60em;
    font-family: $post-font-style;

    .post-details {
      padding-bottom: 10px;
    }

    article {
      font-size: 1.2em;
    }
  } 
}

/* Aside */

aside {
  background: #fff;
  width: 100%;
  z-index: 2;
  border-bottom: 1px solid lighten($light, 30%);
  box-shadow: 0px 1px 1px RGBA(4, 25, 54, .1);

  nav {
    display: flex;
    justify-content: center;
    font-size: 1em;

    ul {
      margin: 0;
      padding: 0;
      list-style: none;

      li {
        float: left;
        position: relative;

        a.nav-link {
          text-decoration: none;
          color: $light;
          display: block;
          padding: 15px;
          font-family: 'Muli', sans-serif;
          line-height: $nav-height;
          margin-bottom: -1px;
          box-shadow: 0;
          transition: 0.3s;
          -webkit-transition: 0.3s;

          &:hover,
          &.active {
            background: $header;
            text-decoration: none;
            color: $main;
          }
        }
      }
    }

    &:after {
      content: '';
      display: table;
      clear: both;
    }
  }
}

.sub {
  font-size: 0.95em;
  margin-top: 0;
  margin-bottom: 5px;

  a {
    text-decoration: none;
    margin: 2px;
  }
}

.emph {
  color: $link-hover-color;
}

.content-box {
  border-radius: 5px;
  margin: auto;
  justify-content: center;

  .content {
    padding: 0 10px;
  }
}

.award {
  display: inline-block;
  background: $award-color;
  color: white;
  border-radius: 5px;
  padding: 0 3px;
  margin-left: 5px;
  font-weight: bold;
  font-size: 0.9em;
}

.degree {
  @extend h4;
}

figure {
  // display: table;
  margin: 25px auto;
  display: grid;
  justify-content: center;

  figcaption {
    font-size: 0.8em;
    padding: 0 ;
    color: $caption-color;
    font-family: Avenir;
  }

  img, figcaption {
    // display: table-row;
    max-width: 40vw;
  }

  &.wide {
    display: table;
    position: relative;
    float: right;
    overflow-x: scroll;

    img, figcaption {
      display: table-row;
      position: relative;
      max-width: 70vw;
    }
  }
}

ul {
  list-style-type: circle;
}

.contact {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  color: $heading-font-color;
  a {
    font-size: 1.5em !important;
    text-decoration: none;
  }
}

footer {
  color: $light;
  text-align: center;
  padding: 40px;

  .address {
    font-size: 0.8em;
    color: $heading-font-color;
  }
}

.fa, .fab, .far, .fas, .ai {
  margin: 5px;
  text-decoration: none;
}

.visible-hidden {
  clip: rect(1px, 1px, 1px, 1px);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 0px;
}

.visible-hidden:focus {
  clip: auto;
  height: auto;
  overflow: auto;
  position: absolute;
  width: auto;
}

.artifact-list {
  margin-top: 0;
  margin-left: 30px;
  list-style-type: none;
  li:not(:first-child) {
    margin-top: 5px;
  }

  .artifact {
    padding-bottom: 10px;
  }

  .authors {
    color: $link-hover-color;
  }

  .artifact-title > a {
    text-decoration: none;
  }

  details {
    & > summary {
      outline: none;
      margin-bottom: 5px;
      cursor: pointer;
    }

    p {
      padding: 5px;
      border: 1px grey solid;
      border-radius: 5px;
      color: black;
      list-style-type: none;
    }
  }
}

.banner {
  padding: 10px;
  border-radius: 5px;
  border: $light solid 2px;
  background-color: $main-transparent;
}

.grey {
  opacity: 0.3;
}

code {
  color: $link-hover-color;
  font-size: 1rem;
}

.tag {
  font-size: 0.8em;
  font-family: 'Quicksand', 'Helvetica', sans-serif;
  padding: 2px 5px;
  margin: 5px;
  background-color: $main-transparent;
  border: 0.5pt solid $light;
  border-radius: 8px;
  cursor: pointer;

  &.active {
    background-color: $header;
    a {
      color: $main;
    }
  }
}

table {
  margin: 20px auto;
  border-collapse: collapse;
  font-size: 0.9em;
  th {
    border-top: 1pt solid;
    border-bottom: 1pt solid;
    background-color: $font-color;
    color: $main;
  }
  tr {
    border-bottom: 0.5pt solid;
  }
  tr > td, tr > th {
    padding: 0.5em;
    &:not(:last-child) {border-right: 1pt solid;}
  }
}

.course {
  table {
    tr:nth-child(4n), tr:nth-child(4n-1) {
      background: lightgrey;
    }
  }
}

.coursenotes {
  font-size: 1.2rem;
  font-family: $post-font-style;
}

.callout {
  border-left: 4px solid;
  border-radius: 4px;
  padding: 0.5rem;

  &::before {
    display: block;
    font-weight: bold;
    font-size: 0.8em;
    font-family: $heading-font;
  }

  &.ponder {
    &::before {
      content: "PONDER";
      color: #d3a10b;
    }

    border-color: #e7af06;
    background-color: rgba(255, 235, 130, 0.2);
  }
}

blockquote {
  font-style: normal;
  color: #5e5c5c;
  margin: 0;
  padding: 0 5px 0 8px;
  border-left: 2pt solid $link-hover-color;
}
