@import url('https://fonts.googleapis.com/css?family=Quicksand');
@import url('https://fonts.googleapis.com/css2?family=Merriweather:ital,wght@0,300;0,700;1,300&display=swap');

$content-width: 65%;
$header-height: 50px;
$main: #F9CF00;
$main-transparent: rgba(255, 238, 0, 0.4);
$font-style: 'Avenir', sans-serif;
$post-font-style: 'EB Garamond', serif;
$font-color: #000000;
$link-color: #425469;
$caption-color: #727375;
$light: #E61726;
$link-hover-color: $light;
$heading-font: 'Muli', sans-serif;
$heading-font-color: $link-color;
$header: #000000;
$award-color: #0CAC1C;
$conf-color: #1E90FF;
$journal-color: #8A2BE2;
$other-color: #FF1493;
$diss-color: #FF8C00;
$nav-height: 20px;

*,
*::before,
*::after {
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box;
}


